.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: 6rem;
  padding: 1.8rem 8.0rem;
  border-radius: 60px;
  background: #009770;
  color: #FFF;
  font-family: "Yu Gothic";
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-align: left;
  white-space: nowrap;
  text-decoration: none;
  border: none;
  outline: none;
  transition: all 0.2s ease-in;
  overflow: hidden;
  cursor: pointer;
  z-index: 0;
}

@media only screen and (max-width: 768px) {
  .btn {
    max-width: 33.5rem;
    width: 100%;
    padding: 1.6rem 2.0rem 1.6rem 2.4rem;
  }
}

.btn i {
  font-size: 1.8rem;
  margin-right: 0.8rem;
}

.btn span {
  position: relative;
  display: inline-block;
  color: inherit;
  text-decoration: none;
  transition: all 0.2s ease-in;
  padding-top: 0.2rem;
  z-index: 3;
}

.btn svg {
  position: relative;
  display: inline-block;
  width: 2.4rem;
  height: auto;
  text-decoration: none;
  margin-left: 2.0rem;
  z-index: 3;
}

.btn svg path {
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
}

.btn.bg-gray {
  background: #EDEDED;
}

.btn.out-link svg {
  transform: rotate(-45deg);
  transform-origin: center center;
}

.btn::before {
  position: absolute;
  top: 0;
  left: -75%;
  z-index: 2;
  display: block;
  content: '';
  width: 50%;
  height: 100%;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  -webkit-transform: skewX(-25deg);
  transform: skewX(-25deg);
}

.btn:hover {
  color: #FFFFFF;
}

.btn:hover svg path {
  stroke: #FFFFFF;
}

.btn:hover::before {
  animation: shine 1.2s;
}

.white.btn {
  color: #009770;
  background: #FFF;
  border-radius: 90px;
  border: 2px solid #009770;
}

.white.btn::before {
  background: linear-gradient(to right, rgba(213, 231, 226, 0.44) 0%, rgba(255, 255, 255, 0.3) 100%);
  z-index: -2 !important;
}

.white.btn:hover {
  color: #009770;
}

