  align-items: center;
  justify-content: center;
  padding: 0.2rem 0.6rem;
  width: 3.0rem;
  height: 3.0rem;
  background-color: #FED517;
  border-radius: 50%;
  font-size: 1.6rem;
  line-height: 1;
  z-index: 3;
}

.modal .modal-body {
  position: relative;
  padding: 2.0rem;
}

.modal.show {
  opacity: 1;
}

.modal.show .modal-dialog {
  transform: translate(0, 0);
}

.modal-open {
